width: nth($ticks-position, 2);\n            height: calc(50% + #{$item-vertical-spacing});\n            border-color: $ticks-color;\n            border-style: solid;\n        }\n        &::before {\n            top: #{-2 * $ticks-width};\n            border-width: 0 0 $ticks-width $ticks-width;\n        }\n        &::after {\n            top: 50%;\n            border-width: 0 0 0 $ticks-width;\n        }\n        &:first-child::before {\n            top: #{-$group-padding - $ticks-width};\n            height: calc(50% + #{$group-padding + $item-vertical-spacing});\n        }\n        &:last-child::before {\n            border-radius: 0 0 0 #{2 * $ticks-width};\n        }\n        &:last-child::after {\n            display: none;\n        }\n    }\n}\n\n// import\n// endimport\n.query-builder,\n.query-builder * {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\n.query-builder {\n    font-family: sans-serif;\n}\n\n.query-builder .hide {\n    display: none;\n}\n\n.query-builder .pull-right {\n    float: right !important;\n}\n\n.query-builder .btn {\n    text-transform: none;\n    display: inline-block;\n    padding: 6px 12px;\n    margin-bottom: 0px;\n    font-size: 14px;\n    font-weight: 400;\n    line-height: 1.42857;\n    text-align: center;\n    white-space: nowrap;\n    vertical-align: middle;\n    touch-action: manipulation;\n    cursor: pointer;\n    user-select: none;\n    background-image: none;\n    border: 1px solid transparent;\n    border-radius: 4px;\n}\n\n.query-builder .btn.focus,\n.query-builder .btn:focus,\n.query-builder .btn:hover {\n    color: #333;\n    text-decoration: none;\n}\n\n.query-builder .btn.active,\n.query-builder .btn:active {\n    background-image: none;\n    outline: 0px none;\n    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.125) inset;\n}\n\n.query-builder .btn-success {\n    color: #FFF;\n    background-color: #5CB85C;\n    border-color: #4CAE4C;\n}\n\n.query-builder .btn-primary {\n    color: #FFF;\n    background